<template>
  <div class="container">
    <div v-if="recipe">
      <div class="recipe-header mt-3 mb-4">
        <h1>{{ recipe.title }}</h1>
        <img :src="recipe.image" class="center" />
      </div>
      <div class="recipe-body">
        <div class="wrapper">
          <div class="wrapped">
            <div class="mb-3">
              <div>Ready in {{ recipe.readyInMinutes }} minutes</div>
              <div>Likes: {{ recipe.aggregateLikes }} likes</div>
            </div>
            Ingredients:
            <ul>
              <li v-for="r in recipe.extendedIngredients" :key="r.id">
                {{ r.original }}
              </li>
            </ul>
          </div>
          <div class="wrapped">
            Instructions:
            <ol>
              <li
                v-for="s in recipe.analyzedInstructions[0].steps"
                :key="s.number"
              >
                {{ s.step }}
              </li>
            </ol>
          </div>
        </div>
      </div>
      <pre>
      {{ $route.params }}
      {{ recipe }}
    </pre
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      recipe: null
    };
  },
  async mounted() {
    try {
      let response = await this.axios.get(
        "https://test-for-3-2.herokuapp.com/recipes/info",
        {
          params: { id: this.$route.params.recipeId }
        }
      );
      this.recipe = response.data.recipe;
    } catch (error) {
      console.log(error);
    }
  }
};
</script>

<style scoped>
.wrapper {
  display: flex;
}
.wrapped {
  width: 50%;
}
.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
/* .recipe-header{

} */
</style>
